body {
  font-family: "Karla";
  width: 100%;
  height: 100vh;
  justify-content: center;
}

#root {
  background-image: url(/static/media/bg-top.4980c1fd.svg);
  background-repeat: no-repeat;
  background-position: 200px -40px;
  background-color: hsl(240, 78%, 98%);
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 200vh;
  align-items: center;
}

.headerContainer {
  display: flex;
  /* background-color: blue; */
  width: 80%;
  height: 30vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  /* text-align: center; */
}

.titleContainer {
  text-align: center;
  width: 80%;
}

.title {
  font-weight: 700;
  color: hsla(0, 0%, 0%, 0.5);
}

.toggleBtnContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  /* background-color: red; */
  color: hsla(0, 0%, 0%, 0.5);
  font-weight: 700;
}

/* The switch - the box around the slider */

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: 0.4s;
  background: linear-gradient(to right, hsl(236, 72%, 79%), hsl(237, 63%, 64%));
}

.slider:before {
  position: absolute;
  content: "";
  height: 23px;
  width: 24px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/* PLAN CONTAINERS */

.mainContainer {
  display: grid;
  place-content: center;
  width: 100%;
  grid-template-columns: 80%;
  grid-template-rows: repeat(3, auto);
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
}

.planContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
  /* background-color: red; */
  border-radius: 0.5rem;
  box-shadow: 0.1px 0.1px 0.5rem hsl(0, 0%, 0%, 0.3);
}

/* APPLYING DIFFERENT STYLES TO THE PROFESSINAL CONTAINER */

#Professional {
  background-image: linear-gradient(to bottom, hsl(236, 72%, 79%), hsl(237, 63%, 64%));
  /* background-color: red; */
}

#Professional * {
  color: white;
}

#Professional > .learnMoreBtn {
  background-image: linear-gradient(to bottom, white, white);
  color: hsl(237, 66%, 72%);
  border: 1px solid white;
}

#Professional > .benefitContainer > .planBenefits,
#Professional > .planPrice {
  border-bottom: 0.1rem solid hsla(0, 0%, 100%, 0.3);
}

.planName {
  color: hsla(0, 0%, 0%, 0.6);
  font-size: 1.1rem;
}

.planPrice {
  color: hsla(0, 0%, 0%, 0.7);
  font-weight: 800;
  font-size: 3rem;
  width: 90%;
  text-align: center;
  height: 10vh;
  border-bottom: 0.1rem solid hsla(0, 0%, 0%, 0.3);
  margin-bottom: -15px;
  padding: 0;
}

.benefitContainer {
  width: 100%;
  padding: 0;
  height: 20vh;
  list-style: none;
  /* row-gap: 220px; */
}

.planBenefits {
  margin-right: auto;
  margin-left: auto;
  /* background-color: blue; */

  width: 90%;
  height: 7vh;
  text-align: center;
  border-bottom: 0.1rem solid hsla(0, 0%, 0%, 0.3);
  box-sizing: border-box;
  line-height: 2.5rem;
  font-weight: 800;
  color: hsla(0, 0%, 0%, 0.5);
  font-size: 0.8rem;
}

.learnMoreBtn {
  border-radius: 0.5rem;
  color: white;
  font-weight: 700;
  background-color: hsl(237, 66%, 72%);
  background-image: linear-gradient(to right, hsl(236, 72%, 79%), hsl(237, 63%, 64%));
  width: 80%;
  height: 6vh;
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
  border: none;
  cursor: pointer;
}

@media screen and (min-width: 45rem) {
  #root {
    height: 100vh;
    background-image: url(/static/media/bg-bottom.0ed1debd.svg), url(/static/media/bg-top.4980c1fd.svg);
    background-repeat: no-repeat;
    background-position: 0% 100%, 100% 0%;
  }

  .headerContainer {
    width: 20rem;
  }

  .mainContainer {
    height: 60vh;
    grid-template-rows: 55vh;
    grid-template-columns: repeat(3, minmax(200px, 300px));
    /* background-color: red; */
    align-items: baseline;
  }

  #Professional {
    /* margin-top: -20px; */
    padding-top: 20px;
    /* height: min-content; */
    z-index: 10;
  }

  #Professional > .learnMoreBtn {
    margin-bottom: 3rem;
  }

  .planContainer {
    box-shadow: 0.1px 0.1px 0.1rem hsl(0, 0%, 0%, 0.3);
    background-color: white;
  }
}

